<template>
  <div class="container main">
    <Content class="left my-shadow" padding="0">
      <!-- <nav>
        <ul>
          <li v-for="item in list" :key="item">{{item}}</li>
        </ul>
      </nav>-->
    </Content>
    <Content class="right">
      <!-- 商城的内容写在这里就可以 -->
      <vueWaterfallEasy :imgsArr="list" srcKey="img" />
      <!-- <Loader :loading="loading" /> -->
    </Content>
  </div>
</template>

<script>
import vueWaterfallEasy from "vue-waterfall-easy";
import Loader from "@/components/Components/Loader";
import { getGifts } from "@/api/apis";
export default {
  data() {
    return {
      list: [],
      currentPage: 1,
      pageSize: 10,
      loading: false
    };
  },
  created() {
    this.getData({
      currentPage: this.currentPage,
      pageSize: this.pageSize
    });
  },
  components: {
    vueWaterfallEasy
  },
  methods: {
    getData(info) {
      this.loading = true;
      getGifts(info).then(res => {
        this.list = res.data.data.data;
        console.log(this.list);
        this.loading = false;
      });
    }
  }
};
</script>

<style scoped>
nav {
  width: 240px;
  background-color: #fff;
  padding: 20px 0;
}
nav li {
  list-style: none;
  padding: 10px 30px;
  cursor: pointer;
  transition: all 0.2s;
}
nav li:hover {
  background-color: #f2f2f2;
  padding-left: 40px;
  font-weight: bold;
}
.left {
  /* width: 240px; */
  margin-right: 20px;
  height: fit-content;
}
.right {
  width: 100%;
  min-height: 100vh;
  /* background-color: #fff; */
  padding: 40px;
  /* display: flex; */
  /* flex-direction: column; */
  /* align-items: center; */
}
</style>


